<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center" :style="{ backgroundImage: `url(${ item.imageUrl })` }">
                    </div>
                    <div class="card-body">
                        <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
                        <h5 class="card-title">
                            <a href="#" class="text-dark">{{ item.title }}</a>
                        </h5>
                        <p class="card-text">{{ item.content }}</p>
                        <div class="d-flex justify-content-between align-items-baseline">
                            <!-- <div class="h5">2,800 元</div> -->
                            <del class="h6" v-if="!item.price">原價 {{ item.origin_price | currency }} 元</del>
                            <del class="h6" v-if="item.price">原價 {{ item.origin_price | currency }} 元</del>
                            <div class="h5" v-if="item.price">現在只要 {{ item.price | currency }} 元</div>
                        </div>
                    </div>
                    <div class="card-footer d-flex">
                        <button type="button" class="btn btn-outline-secondary btn-sm" @click="getProduct(item.id)">
                            <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                            查看更多
                        </button>
                        <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
                            <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                            加到購物車
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <div class="my-5 row justify-content-center">
            <div class="col-md-6">

                <table class="table">
                    <thead>
                        <th></th>
                        <th>品名</th>
                        <th>數量</th>
                        <th>單價</th>
                    </thead>
                    <tbody>
                        <tr v-for="item in cart.carts" :key="item.id">
                            <td class="align-middle">
                                <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCart(item.id)">
                                    <i class="far fa-trash-alt"></i>
                                </button>
                            </td>
                            <td class="align-middle">
                                {{ item.product.title }}
                                <div class="text-success" v-if="item.coupon">已套用優惠券</div>
                            </td>
                            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                            <td class="align-middle text-right">{{ item.final_total }}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td colspan="3" class="text-right">總計</td>
                            <td class="text-right">{{ cart.total }}</td>
                        </tr>
                        <tr v-if="cart.final_total !== cart.total">
                            <td colspan="3" class="text-right text-success">折扣價</td>
                            <td class="text-right text-success">{{ cart.final_total }}</td>
                        </tr>
                    </tfoot>
                </table>

                <div class="input-group mb-3 input-group-sm">
                    <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
                    <div class="input-group-append">
                        <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
                            套用優惠碼
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="createOrder">
                <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" id="useremail" name="email"
                        v-model="form.user.email"
                        v-validate="'required|email'" :class="{'is-invalid' : errors.has('email') }" placeholder="請輸入 Email">
                    <span class="text-danger">{{ errors.first('email') }}</span>
                </div>

                <div class="form-group">
                    <label for="username">收件人姓名</label>
                    <input type="text" class="form-control" id="username" name="name"
                        v-model="form.user.name"
                        v-validate="'required'" :class="{'is-invalid' : errors.has('name') }" placeholder="輸入姓名">
                    <span class="text-danger" v-if="errors.has('name')">必須輸入姓名</span>
                </div>

                <div class="form-group">
                    <label for="usertel">收件人電話</label>
                    <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="請輸入電話">
                </div>

                <div class="form-group">
                    <label for="useraddress">收件人地址</label>
                    <input type="address" class="form-control" id="useraddress" v-model="form.user.address"
                        placeholder="請輸入地址">
                    <span class="text-danger">地址欄位不得留空</span>
                </div>

                <div class="form-group">
                    <label for="usermsg">留言</label>
                    <textarea name="" id="" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                    <button class="btn btn-block btn-danger">送出訂單</button>
                </div>
            </form>
        </div>

        <!-- 產品 modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
            aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <img :src="product.imageUrl" class="img-fluid" alt="">
                        <blockquote class="blockquote mt-3">
                            <p class="mb-0">{{ product.content }}</p>
                            <footer class="blockquote-footer text-right">{{ product.description }}</footer>
                        </blockquote>
                        <div class="d-flex justify-content-between align-items-baseline">
                            <div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
                            <del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
                            <div class="h4" v-if="product.price">現在只要 {{ product.price }} 元</div>
                        </div>
                        <select name="" class="form-control mt-3" v-model="product.num">
                            <option :value="num" v-for="num in 10" :key="num">
                                選購 {{num}} {{product.unit}}
                            </option>
                        </select>
                    </div>
                    <div class="modal-footer">
                        <div class="text-muted text-nowrap mr-3">
                            小計 <strong>{{ product.num * product.price }}</strong> 元
                        </div>
                        <button type="button" class="btn btn-primary" @click="addtoCart(product.id, product.num)">
                            <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                            加到購物車
                        </button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
    /* eslint-disable */
    import $ from 'jquery';

    export default {
        name: 'CustomerOrder',
        data() {
            return {
                products: [],
                product: {},
                cart: {},
                coupon_code: '',
                form: {
                    "user": {
                        "name": "",
                        "email": "",
                        "tel": "",
                        "address": ""
                    },
                    message: ""
                },
                status: {
                    loadingItem: ''
                },
                isLoading: false
            }
        },
        methods: {
            getProducts(page = 1) {
                const api =
                    `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products?page=${page}`;
                const vm = this;
                vm.isLoading = true;
                this.$http.get(api).then((res) => {
                    // console.log(res.data);
                    if (res.data.success) {
                        vm.isLoading = false;
                        vm.products = res.data.products;
                        // vm.pagination = res.data.pagination;
                    }
                })
            },
            getProduct(id) {
                const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
                const vm = this;
                vm.status.loadingItem = id;
                this.$http.get(api).then((res) => {
                    // console.log(res.data);
                    if (res.data.success) {
                        vm.status.loadingItem = '';
                        vm.product = res.data.product;
                        // vm.pagination = res.data.pagination;
                        $('#productModal').modal('show');
                    }
                })
            },
            addtoCart(id, qty = 1) {
                const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
                const vm = this;
                const cart = {
                    'product_id': id,
                    qty
                };
                vm.status.loadingItem = id;
                this.$http.post(api, {
                    data: cart
                }).then((res) => {
                    // console.log(res.data);
                    if (res.data.success) {
                        vm.status.loadingItem = '';
                        vm.getCart();
                        $('#productModal').modal('hide');
                    }
                })
            },
            getCart() {
                const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
                const vm = this;
                vm.isLoading = true;
                this.$http.get(api).then((res) => {
                    // console.log(res.data);
                    if (res.data.success) {
                        console.log('getCart');
                        vm.isLoading = false;
                        vm.cart = res.data.data;
                    }
                })
            },
            removeCart(id) {
                const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
                const vm = this;
                this.$http.delete(api).then((res) => {
                    // console.log(res.data);
                    if (res.data.success) {
                        vm.getCart();
                    }
                })
            },
            addCouponCode() {
                const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`;
                const vm = this;
                this.$http.post(api, {
                    data: {
                        code: vm.coupon_code
                    }
                }).then((res) => {
                    console.log('優惠券狀態', res.data);
                    if (res.data.success) {
                        vm.getCart();
                    }
                })
            },
            createOrder() {
                const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
                const vm = this;
                const order = vm.form;
                this.$validator.validate().then((result) => {
                    console.log(result);
                    if (result) {
                        console.log('欄位完整!!');
                        // this.$http.post(api, {
                        //     data: order
                        // }).then((res) => {
                        //     console.log(res);
                        //     if (res.data.success) {
                        //         console.log('訂單已成立', res.data);
                        //     }
                        // })
                    } else {
                        console.log('欄位不完整');
                    }
                });
            },
        },
        created() {
            this.getProducts();
            this.getCart();
        }
    }
</script>